import Vue from 'vue'

const isServer = Vue.prototype.$isServer

/** dom */
export const on = (() => {
  if (!isServer && document.addEventListener) {
    return (element, event, handler) => {
      if (element && event && handler) {
        element.addEventListener(event, handler, false)
      }
    }
  } else {
    return (element, event, handler) => {
      if (element && event && handler) {
        element.attachEvent(`on${event}`, handler)
      }
    }
  }
})()

/**  */
export const addClass = (element, cls) => {
  if (!element) {
    return
  }
  const curCls = element.className
  /**  */
  const curClasses = (curCls || '').split(' ')
  /**  */
  const classes = (cls || '').split(' ')
  /**  */
  const classSet = new Set([
    ...curClasses,
    ...classes
  ])
  /**  */
  element.className = Array.from(classSet).join(' ')
}

export const removeClass = (element, cls) => {
  if (!element) {
    return
  }
  const curCls = element.className
  /**  */
  const curClasses = (curCls || '').split(' ')
  /**  */
  const classes = (cls || '').split(' ')
  /**  */
  const newClasses = curClasses.filter(c => classes.indexOf(c) === -1)
  /**  */
  element.className = newClasses.join(' ')
}
